.skip-menu {
  list-style: none;
  position: absolute;
  width: 100%;
}

.skip-menu a {
  color: #ffffff;
  position: absolute;
  top: -200em;
}

.skip-menu a:focus {
  background: #268744;
  border-radius: 100px;
  left: 20px;
  outline: 0;
  padding: 8px 24px;
  position: absolute;
  top: auto;
  z-index: 11;
}

@media (min-width: 576px) {
  .page-background {
    background: linear-gradient(to right, #f8f8f8 50%, #ffffff 50%);
  }
}

.accessibility {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  margin: 0;
  padding-left: 0;
}

.accessibility a {
  color: #333333;
  font-size: 11px;
  font-weight: 700;
  text-transform: uppercase;
}

.accessibility a:not(:last-child) {
  margin-right: 16px;
}

.accessibility a:hover {
  text-decoration: none;
}

.accessibility a:hover .accessibility-text {
  text-decoration: underline;
}

@media (min-width: 576px) {
  .accessibility img {
    max-width: 16px;
  }
}

.logo {
  justify-content: center;
}

.logo-image {
  flex-shrink: 0;
}

.nav-description,
.page-description {
  font-size: 16px;
  line-height: 28px;
  text-align: right;
  color: #555555;
  font-style: italic;
  font-weight: 400;
  font-family: Rawline;
}

.page-description {
  text-align: left;
}

.content-title {
  font-size: 32px;
  line-height: 36px;
  color: #1351b4;
  font-style: normal;
  font-weight: 500;
  font-family: Rawline;
}

.page-content {
  font-size: 16px;
  line-height: 28px;
  color: #555555;
  font-style: normal;
  font-weight: 400;
  font-family: Rawline;
}
